.homepageContainer {
    padding: 10px;
}

.fieldContainer {
    display:flex;
    justify-content: center;
}

.fieldContainer > div {
    position: relative;
    width: 100%;
    max-width: 300px;
    margin-top: 10px;
}

.checkBox {
    margin-left: 5px;
}

.line {
    background: black;
    height: 2px;
    width: 100%;
}

.keyword {
    position: absolute;
    top: -5px;
    right: -10px;
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif !important;
    font-size: 0.9rem;
    background-color: var(--secondary);
    padding: 3px 5px 3px 17px;
    z-index: 2;
}

.currency {
    position: absolute;
    top: 5px;
    left: 25%;
    /* font-family:'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif !important;
font-size: 0.9rem; */
}

.price {
    padding: 7px;
    width:100%;
}

.format {
    position: absolute;
    top:5px;
    left:90px;
    background-color: var(--secondary);
    padding: 3px 5px 3px 17px;
    z-index: 2;
}

.start {
    width: 100%;
    max-width:300px;
    margin-top: 15px;
}